<template>
  <div>
    <v-layout>
      <v-flex>
        <h3>Executive Summary</h3>
      </v-flex>
    </v-layout>
    <v-layout>
      <v-layout row wrap>
        <v-flex xs12 md4 pr-1>
          <v-select v-model="inEntity" :items="entities" item-text="entity_name" item-value="id" label="Entity"></v-select>
        </v-flex>
        <v-flex xs12 md8>
          <v-autocomplete v-model="inPractice" :items="filteredPractices" persistent-hint item-text="group_practice" item-value="id" label="Practice"></v-autocomplete>
        </v-flex>
      </v-layout>
      <v-flex>
        <v-text-field v-model="inAuditDt" label="Audit Date (MMYYYY)"></v-text-field>
      </v-flex>
      <v-flex>
        <v-btn @click="generateReport()">Run</v-btn>
      </v-flex>
    </v-layout>
    <div id="toPrint">
      <v-container fluid>
        <v-layout>
          <v-flex cols="2">
            <img width="125" src="../assets/logo.png" />
          </v-flex>
          <v-flex>
            <div
              style="
                font-size: 2em;
                font-weight: bold;
                margin-left: 150px;
                padding-top: 15px;
              "
            >
              {{ reportTitle }}
            </div>
          </v-flex>
        </v-layout>
        <v-divider></v-divider>
        <div class="provider">
          <v-layout dense>
            <v-flex xs9 cols="9">
              <div>
                <span class="heading">Practice Name:</span>
                {{ reportData[0].group_practice }}
              </div>
            </v-flex>
            <v-flex xs3 cols="3">
              <div>
                <span class="heading">Risk Score:</span>
                {{ computePracticeScore }}
              </div>
            </v-flex>
          </v-layout>
          <v-layout dense>
            <v-flex xs9 cols="9">
              <div>
                <span class="heading">Entity:</span>
                {{ reportData[0].entity_name }}
              </div>
            </v-flex>
            <v-flex>
              <div>
                <span class="heading">Risk Level:</span>
                {{ computePracticeAuditLevel }}
              </div>
            </v-flex>
          </v-layout>
          <v-layout dense>
            <v-flex xs9 cols="9">
              <div>
                <span class="heading">Number of Records Reviewed:</span>
                {{ computeTotalRows }}
              </div>
            </v-flex>
          </v-layout>
        </div>
        <v-layout dense class="center-title">
          <v-flex xs2 cols="2">
            <span class="heading">Provider Name</span>
          </v-flex>
          <v-flex>
            <span class="heading">NPI</span>
          </v-flex>
          <v-flex>
            <span class="heading">Audit Completed Date</span>
          </v-flex>
          <v-flex>
            <span class="heading center-title">Audit Type</span>
          </v-flex>
          <v-flex>
            <span class="heading center-title"># of Records</span>
          </v-flex>
          <v-flex>
            <span class="heading center-title">Score</span>
          </v-flex>
          <v-flex>
            <span class="heading center-title">Risk Level</span>
          </v-flex>
          <v-flex xs3 cols="3">
            <span class="heading">Issues Identified</span>
          </v-flex>
        </v-layout>
        <div v-for="(row, indx) in reportData" :key="indx">
          <v-layout dense>
            <v-flex xs2 cols="2">{{ row.first_name }} {{ row.last_name }}, {{ row.title }}</v-flex>
            <v-flex>{{ row.npi }}</v-flex>
            <v-flex class="center-title">{{ row.completed_date }}</v-flex>
            <v-flex class="center-title">{{ row.audit_type }}</v-flex>
            <v-flex class="center-title">{{ row.row_count }}</v-flex>
            <v-flex class="center-title">{{ computeProviderScore(row.audit_score, row.row_count) }}</v-flex>
            <v-flex class="center-title">{{ computeRisk(computeProviderScore(row.audit_score, row.row_count)) }}</v-flex>
            <v-flex xs3 cols="3">{{ formatFailedReason(row.fail_reason) }}</v-flex>
          </v-layout>
        </div>
      </v-container>
    </div>
  </div>
</template>

<script>
import httpService from "../httpService";
import html2pdf from "html2pdf.js";

import Config from "../config";

export default {
  data: () => ({
    reportTitle: "Executive Summary Report",
    reportData: [
      {
        npi: "",
        last_name: "",
        first_name: "",
        title: "",
        physician_entity_id: "",
        id: 0,
        entity_name: "",
        group_practice: "",
        audit_date: "",
        completed_date: null,
        audit_type: null,
        physician_audit_id: 0,
        audit_score: 0,
        row_count: 0,
        fail_reason: [
          {
            failed_reason: "",
          },
        ],
      },
    ],
    inEntity: "",
    inPractice: "",
    inAuditDt: "",
  }),
  mounted() {},
  computed: {
    computeTotalRows() {
      return this.reportData.reduce((sum, count) => {
        return sum + count.row_count;
      }, 0);
    },
    entities() {
      return this.$store.getters.ENTITY;
    },
    practices() {
      return this.$store.getters.PRACTICE;
    },
    filteredPractices() {
      return this.practices.filter((obj) => obj.entity_id === this.inEntity);
    },
    computePracticeAuditLevel() {
      return this.computeRisk(this.computePracticeScore);
    },
    computePracticeScore() {
      // TODO - function in mixins (params deductions, totalrows)

      // rows = number of providers
      if (this.computeTotalRows == 0) {
        return "";
      } else {
        const totalScore = this.reportData.length * 100;
        let totalDeductions = 0;
        for (let i = 0; i < this.reportData.length; i++) {
          totalDeductions += 100 - this.computeProviderScore(this.reportData[i].audit_score, this.reportData[i].row_count).toFixed(0);
        }

        return ((totalScore - totalDeductions) / this.reportData.length).toFixed(0);
      }
    },
  },
  methods: {
    sleep(ms) {
      return new Promise((resolve) => setTimeout(resolve, ms));
    },
    formatFailedReason(reasons) {
      const arr = [...new Set(reasons.map((item) => item.failed_reason.split(",")))].flat();
      return arr
        .filter((value, index, self) => {
          return self.indexOf(value) === index;
        })
        .toString()
        .replace(/,/g, ", ");
    },
    generateReport() {
      // api built to receive names.  TODO - switch to id later...
      const entity = this.entities.find((el) => el.id === this.inEntity).entity_name;
      const practice = this.practices.find((el) => el.id === this.inPractice).group_practice;

      httpService.get(`${Config.apiRoot}/reports/executive-summary/${encodeURIComponent(entity)}/${encodeURIComponent(practice)}/${this.inAuditDt}`, async (status, data) => {
        console.log("status - ", status);
        if (data.length == 0) {
          return;
        }
        this.reportData = data;
        await this.sleep(2000); // seems to need this delay for display of PDF

        var element = document.getElementById("toPrint");
        var opt = {
          margin: [0, 0, 0, 0.25],
          filename: "ExecutiveSummary.pdf",
          image: { type: "jpeg", quality: 1 },
          html2canvas: { scale: 2 },
          pagebreak: { mode: "avoid-all" },
          jsPDF: { unit: "in", format: "letter", orientation: "landscape" },
        };

        // New Promise-based usage:
        html2pdf()
          .set(opt)
          .from(element)
          .save();
      });
    },
  },
};
</script>

